.pjts{
    margin-top: 1.563vw;
    --pborderw:0.156vw;
    --pborders:solid;
    --pborderc: $white;
    display: grid;
    grid-template-areas: "gtn efs ecm""gtn ptf ptf";
    button{
        border-color: $white;
        color: $white;
    }
    h3{
        margin-top: 1.302vw;
        margin-bottom: 0.99vw;
    }
    .pjl-c{
        --arwcborderwidth:0.156vw;
        border-width: var(--arwcborderwidth);
        border-style: solid;
        border-color: $white;
        border-radius: 50%;
        --logocwidth: 2.448vw;
        --logocheight: 2.448vw;
        width: var(--logocwidth);
        height: var(--logocheight);
        display: flex;
        align-items: center;
        cursor: pointer;
        .parw{
            --logowidth:1.406vw;
            --logoheight: 1.25vw;
            width: var(--logowidth);
            height: var(--logoheight);
            opacity: 0;
            transition: .3s cubic-bezier(.4,.4,0,1);
            visibility: hidden;
        }
        .parw-p{
            opacity: 1; 
            transform: translateX(calc( (var(--logocwidth)/2) - (var(--logowidth)/2) - (var(--arwcborderwidth) / 2)));
            visibility: visible;
        }
        .parw-s{
            transform: translateX(calc( (var(--logowidth)) * -1 ));
        }
        .parw-e{
            position: absolute;
            box-sizing: border-box;
            display: flex;
        }
    }
    .pjts-c{
        max-width: 31.944vw;
        border-radius: $radius;
        padding: 2.604vw 2.587vw 2.552vw;
        &:hover{
            .pjl-c{
                background-color: $white;
                .parw-p{
                    opacity: 0;
                    visibility: hidden;
                    transform: translateX(calc( (var(--logocwidth)/2)));
                }
                .parw-s{
                    opacity: 1; 
                    visibility: visible;
                    transform: translateX(calc( (var(--logocwidth)/2) - (var(--logowidth)/2) - (var(--arwcborderwidth) / 2)));
                }
            }
        }
    }
    .gtn{
        background-color: $purple;
        color: $white;
        min-height: 45.313vw;
        grid-area: gtn;
        margin-right: 1.563vw;
    }
    .efs{
        background-color: $greyblue;
        min-height: 21.875vw;
        color: $white;
        grid-area: efs;
        margin-bottom: 1.563vw;
        margin-right: 1.563vw;
    }
    .ecm{
        background-color: $yellow;
        min-height: 21.875vw;
        color: $white;
        grid-area: ecm;
        margin-bottom: 1.563vw;
    }
    .ptf{
        background-color: #CFE2CD;
        min-height: 21.875vw;
        color: $black;
        grid-area: ptf;
        min-width: 65.452vw;
        button{
            color: $black;
            border: 0.156vw solid $black;
        }
        .pjl-c{
            border-color: $black;
        }
        &:hover{
            .pjl-c{
                background-color: $black;
            }
        }
    }
}

