.tl{
    margin-top: 1.563vw;
    display: grid;
    grid-template-areas: 'pgp tcs griet';
    .tl-i{
        margin-right: 1.042vw;
        &:last-child{
            margin-right: 0;
        }
    }
    .pgp-i{
        grid-area: pgp;
    }
    .tcs-i{
        grid-area: tcs;
    }
    .griet-i{
        grid-area: griet;
    }
    .tl-c{
        border-radius: $radius;
        width: 31.944vw;
        height: 45.313vw;
        padding: 2.604vw 2.214vw;
        color: $white;
        .tl-cc{
            box-sizing: border-box;
            position: relative;
            display: flex;
        }
        .tl-ci{
            opacity: 0;
            transform: translateY(-1.043vw);
            transition: all .66s cubic-bezier(0.66,0,0.2,1);
            visibility: hidden;
        }
        .tl-collapse{
            opacity: 1; 
            transform: translateY(0);
            transition-delay: .66s;
            visibility: visible;
        }
        .tl-cie{
            position: absolute;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
        }
        button{
            color: $white;
            border-color: $white;
        }
        h3{
            margin:1.042vw 0 0;
        }
        .tl-h{
            padding-bottom: 1.042vw;
            border-bottom: 0.156vw solid $white;
            margin:1.198vw 0 1.042vw;
            display: flex;
            justify-content: space-between;
            align-items: center;
            button{
                color: $white;
            }
        }
        .ls-c{
            .ls{
                width: 2.448vw;
                height: 2.448vw;
                transform: rotate(0deg);
                transition: transform .66s cubic-bezier(0.66,0,0.2,1);
                cursor: pointer;
            }
            .rt{
                transform: rotate(45deg);
            }
        }
        &:hover{
            .ls-c{
                circle{
                    fill:$white;
                }
            }
        }
    }
    .pgp-g{
        background: #34495E;
        &:hover{
            .ls-c{
                line{
                    stroke: #34495E;
                }
            }
        }
    }
    .pgp-d{
        background: linear-gradient(135deg,#4f6f8f,#34495E);
        &:hover{
            .ls-c{
                line{
                    stroke: #34495E;
                }
            }
        }
    }
    .tcs-p{
        background: #049cb7;
        &:hover{
            .ls-c{
                line{
                    stroke: #049cb7;
                }
            }
        }
        .tl-h{
            padding-bottom: 1.042vw;
        }
    }
    .tcs-g{
        background: linear-gradient(135deg,#049cb7,#3dd9bd);
        &:hover{
            .ls-c{
                line{
                    stroke: #049cb7;
                }
            }
        }
    }
    .griet-p{
        background: #E39801;
        &:hover{
            .ls-c{
                line{
                    stroke: #E39801;
                }
            }
        }
    }
    .griet-b{
        background: #E39801;
        background: linear-gradient(135deg,#E39801,$yellow);
                &:hover{
            .ls-c{
                line{
                    stroke: #E39801;
                }
            }
        }
    }
}

@media screen and (max-width: 1280px) {
    .tl{
        margin-top: 2.345vw;
        display: grid;
        grid-template-areas: 'pgp tcs' 'griet griet';
        .tl-i{
            margin-right: 2.344vw;
            margin-bottom: 2.344vw;
            &:nth-child(2){
                margin-right: 0;
            }
            &:last-child{
                margin-right: 0;
                margin-bottom: 0;
            }
        }
        .tl-c{
            border-radius: $radius-d;
            max-width: 48.047vw;
            width: unset;
            height: 63.164vw;
            padding: 3.906vw 3.321vw;
            color: $white;
            h3{
                margin:1.563vw 0 0;
            }
            .tl-h{
                padding-bottom: 1.563vw;
                border-bottom: 0.234vw solid $white;
                margin:1.797vw 0 1.563vw;
            }
            .ls-c{
                .ls{
                    width: 3.672vw;
                    height: 3.672vw;
                }
            }
        }
        .griet{
            max-width: 98.438vw;
            width: unset;
            height: 34.57vw;
        }
    }
}

@media screen and (max-width: 1024px) {
    .tl{
        margin-top: 1.954vw;
        display: grid;
        grid-template-areas: 'pgp tcs' 'griet griet';
        .tl-i{
            margin-right: 1.954vw;
            margin-bottom: 1.953vw;
            &:last-child{
                margin-right: 0;
                margin-bottom: 0;
            }
        }
        .tl-c{
            border-radius: $radius-t;
            max-width: 48.047vw;
            width: unset;
            height: 83.936vw;
            padding: 3.906vw 3.369vw;
            h3{
                margin:1.954vw 0 0;
            }
            .tl-h{
                padding-bottom: 2.246vw;
                border-bottom: 0.292vw solid $white;
                margin:2.246vw 0 1.66vw;
                display: flex;
                justify-content: space-between;
            }
            .ls-c{
                .ls{
                    width: 4.59vw;
                    height: 4.59vw;
                }
            }
        }
        .griet{
            max-width: 98.047vw;
            width: unset;
            height: 43.994vw;
        }
    }
}

@media screen and (max-width: 480px) {
    .tl{
        margin-top: 4.833vw;
        display: grid;
        grid-template-areas: 'pgp' 'tcs' 'griet';
        .tl-i{
            margin-right: 0;
            margin-bottom: 4.831vw;
            &:last-child{
                margin-right: 0;
                margin-bottom: 0;
            }
        }
        .tl-c{
            border-radius: $radius-mc;
            max-width: unset;
            width: unset;
            height: unset;
            padding: 9.662vw 6.763vw;
            h3{
                margin:2.657vw 0 0vw;
            }
            .tl-h{
                padding-bottom: 3.623vw;
                border-bottom: 0.723vw solid $white;
                margin: 3.623vw 0 2.415vw;
                display: flex;
                justify-content: space-between;
                h2{
                    font-size: 4.831vw;
                    line-height: 6.522vw;
                    margin: 0;
                }
            }
            .ls-c{
                .ls{
                    width: 8.938vw;
                    height: 8.938vw;
                }
            }
        }
        .pgp{
            height: 108.333vw;
        }
        .tcs{
            height: 160.749vw;
        }
        .griet{
            width: unset;
            max-width: unset;
            height: 118.478vw;
        }
    }
}